* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary-color: #008083;
  --primary-darker-color: darker(30%, var(--primary-color));
  --dark-color: #2c3131;
  --grey-color: #a4a9a9;
}

body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', 'Helvitica', 'Arial', serif;
}

h1, h2, h3, h4, h5, h6, p, a, span {
  font-family: 'Montserrat', 'Helvitica', 'Arial', serif;
  font-style: normal;
  text-decoration: none;
}

.container {
  max-width: 98%;
  width: 100%;
  display: block;
  margin: 0 auto;
}

/** SMALLER DISPLAYS BREAKPOINT **/
@media (min-width: 384px) {
  .container {
    max-width: 384px;
  }
}

/** MEDIUM DISPLAYS BREAKPOINT **/
@media (min-width: 900px) {
  .container {
    max-width: 800px;
  }
}

/** LARGE DISPLAYS BREAKPOINT **/
@media (min-width: 1200px) {
  .container {
    max-width: 1175px;
  }
}

.header {
    display: block;
    margin: 0 auto;
    text-align: center;
}

.header img {
    display: block;
    margin: 30px auto;
    max-width: 200px;
    width: 100%;
    border-radius: 50%;
    box-shadow: 0 10px 30px rgba(0, 0, 0, .2);
}

.header h1 {
    color: var(--primary-color);
}

.header h2 {
    color: var(--dark-color);
}

.header .location {
    color: var(--dark-color);
    margin: 10px 0;
}

.header p {
    color: var(--grey-color);
}
.skills__section {
    margin: 50px auto;
    max-width: 384px;
    width: 100%;
    display: block;
}
@media (min-width: 900px) {
    .skills__section {
        max-width: 500px;
    }
}

.skills__section h2 {
    color: var(--primary-color);
    text-align: center;
}

.skills__section ul {
    margin: 50px 0;
    list-style-type: none;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
    -webkit-align-content: center;
            align-content: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    grid-template-areas: 
        ". ."
        ". ."
        ". ."
        ". ."
        ". ."
        ". ."
        "last last"
}

.skills__section ul li:last-child {
    grid-area: last; 
}

@media (min-width: 900px) {
    .skills__section ul {
        grid-template-columns: repeat(4, 1fr);
        grid-template-areas:
            ". . . ."
            ". . . ."
            ". . . ."
            "last last last last" 
    }
}

.skills__section img {
    display: block;
    margin: 0 auto;
    max-width: 64px;
    width: 100%;
}
.quote__section {
}

.quote__section ul {
    display: grid;
    grid-template-columns: 1 1fr;
    grid-gap: 20px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    list-style-type: none;
}

@media (min-width: 900px) {
    .quote__section ul {
        grid-template-columns: repeat(3, 1fr);
    }
}

.quote__section li {
    border-left: 1px solid var(--primary-color);
    width: 100%;
    max-width: 100%;
    padding-left: 10px;
    color: var(--grey-color);
}
.languages__section {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px 5px;
    margin: 50px 0 0;
    grid-template-areas: 
        "title title"
        ". ."
        ". ."
        ". ."
        ". ."
        ". ."
        ". ."
        "last last"
}

.languages__section div:last-child {
    grid-area: last;
}

@media (min-width: 900px) {
    .languages__section {
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 50px;
        grid-template-areas: 
            "title title title"
            ". . ."
            ". . ."
            ". . ."
            ". . ."
            ". last ."
    }
    
}

.languages__section h2 {
    grid-area: title;
    text-align: center;
    color: var(--primary-color);
    margin: 0 0 30px;
}
.language img {
    display: block;
    margin: 0 auto;
    max-width: 100px;
    width: 100%;
    border-radius: 50%;
}

@media (min-width: 900px) {
    .language img {
        max-width: 140px;
    }
}

.disabled {
    -webkit-filter: opacity(50%);
            filter: opacity(50%);
}

.language h3 {
    margin: 40px 0 10px;
    text-align: center;
    color: var(--dark-color);
}

.language p {
    text-align: center;
    color: var(--grey-color);
}

